itle .span-line:first-of-type{margin-top:1em}.main-page .container .content .content__title h2{position:relative;font-size:2.4em}.main-page .container .content .content__title h2:before{content:"";position:absolute;width:15px;height:15px;border:2px solid rgba(238,238,238,.58);left:-20px;top:0;border-bottom:none;border-right:none}.main-page .container .content .content__title h2:after{content:"";position:absolute;width:15px;height:15px;border:2px solid rgba(238,238,238,.58);right:-20px;bottom:0;border-top:none;border-left:none}.main-page .container .content .content__title img{width:320px;min-width:100%;max-width:100%}.main-page .container .content .content__area{max-width:480px;width:100%;border:1px solid rgba(180,180,180,.15);border-radius:6px;padding:20px 40px}.main-page .container .content .content__area span{position:relative;font-size:1.2em;font-weight:500}.main-page .container .content .content__area span:before{content:"”";position:absolute;left:-30px;font-size:40px;top:0;color:#ffeba7}.main-page .container .content .content__area span:after{position:absolute;content:"”";font-size:40px;right:-30px;color:#ffeba7}.main-page .container .content button{display:flex;margin-left:auto;margin-right:auto;margin-top:1em}@media screen and (max-width: 1280px){.main-page{margin-top:7.5vh}.main-page .container .content{gap:5em}}@media screen and (max-width: 978px){.main-page .container .short-projects .content{flex-direction:column-reverse}.main-page .container .content{margin-bottom:.4em;margin-top:2em;gap:.1em !important;flex-direction:column}.main-page .container .content .content__title h2{font-size:1.4em}.main-page .container .content .content__area span{font-size:.9em}}